.stack {
  height: 100vh;
  background-color: #1e1e1e;
  &__title {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: #fff;
    font-family: "Inter", sans-serif;
    padding-top: 100px;
    margin: 0;
    @media (min-width: 992px) {
      text-align: left;
      font-size: 40px;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      height: 2px;
      background: linear-gradient(
        132.77deg,
        #e0d2ff 7.48%,
        #efc4fe 27%,
        #e9fffb 58.5%,
        #b7b2f2 92.67%
      );
      @media (min-width: 992px) {
        bottom: 10px;
        left: 250px;
      }
    }
  }

  &__list {
    list-style-type: none;
    color: #fff;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    text-transform: uppercase;
    display: flex;
    flex-wrap: wrap;
    font-weight: 500;

    @media (min-width: 768px) {
      font-size: 24px;
      justify-content: space-around;
    }

    @media (min-width: 1200px) {
      font-size: 36px;
    }
  }

  &__item {
    margin-top: 56px;
    margin-right: 50px;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      right: 0;
      left: 0;
      height: 1px;
      background-color: #fff;
      bottom: 0;
    }
  }
}
